@mixin visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@mixin page-title {
  font-family: $titles-font-family;
  font-weight: 400;
}

@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
